.about-me {
  text-align: center;
  display: grid;
  place-items: center;
  padding: 1.5rem;
  height: 100vh;
  .intro {
    margin-bottom: 2rem;
    .greet {
      color: white;
      h1 {
        mix-blend-mode: difference;
      }
      position: relative;
      &::after {
        content: "";
        position: absolute;
        width: 33%;
        height: 100%;
        background-color: white;
        top: 0;
        z-index: -1;
        color: black;
        animation: negative-slider 5s linear infinite;
        box-shadow: 0px 0px 40px 10px gray;
        border-radius: 50%;
        @media (max-width: 768px) {
          display: none;
        }
      }
    }
    strong {
      color: var(--icon);
    }
    h1 {
      font-size: 3rem;
      font-family: "Shadows Into Light", cursive;
      font-weight: bold;
      width: 100%;
      @media (min-width: 768px) {
        font-size: 8rem;
      }
    }
    p {
      font-size: 2rem;
      color: #4e4e50;
      font-family: 'Orbitron', sans-serif;
    }
  }
  img {
    border: 5px dotted;
    width: 20rem;
    border-radius: 50%;
    aspect-ratio: 1;
  }
  @media (min-width: 1024px) {
    display: flex;
    justify-content: space-around;
    align-items: center;
    flex-direction: row-reverse;
  }
}
